	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Google at the Border","date":"24 Nov 2005","slug":"/2005-11-24-google-at-the-border/","tags":"Y2005"},"html":"<p>Be careful what you put in your weblog. <a href=\"http://www.cbp.gov/\">US Customs and Border Security</a> may not be reading it now, but they may check Google when you try to enter the country, <a href=\"http://hoder.com/weblog/archives/014729.shtml\" title=\"E:M | Goodbye to America\">and you might be refused entry</a>.</p>"},"next":{"fields":{"title":"A Blog Named PapaTech","date":"24 Nov 2005","slug":"/2005-11-24-a-blog-named-papatech/"}},"previous":{"fields":{"title":"Fire The Admins!","date":"25 Nov 2005","slug":"/2005-11-25-fire-the-admins/"}}},{"node":{"fields":{"title":"A Blog Named PapaTech","date":"24 Nov 2005","slug":"/2005-11-24-a-blog-named-papatech/","tags":"Y2005"},"html":"<p>OK, let's see if this works...</p>\n<p>In an attempt to separate the exciting stuff from the boring stuff, I've moved all my tech-related posts to a separate weblog, <a href=\"http://www.papatech.de/\">PapaTech</a> (running on Typo under Rails). You can decide for yourself whether you find the expat/personal stuff at PapaScott to be exciting, or the hip/technical stuff at PapaTech, or neither (in which case, why are you even reading this?).</p>\n<p>I'm going to try to automatically put summaries of the PapaTech posts at PapaScott, so if things break, that means it's not working yet.</p>"},"next":{"fields":{"title":"Hamsters on the Autobahn","date":"23 Nov 2005","slug":"/2005-11-23-hamsters-on-the-autobahn/"}},"previous":{"fields":{"title":"Google at the Border","date":"24 Nov 2005","slug":"/2005-11-24-google-at-the-border/"}}},{"node":{"fields":{"title":"Hamsters on the Autobahn","date":"23 Nov 2005","slug":"/2005-11-23-hamsters-on-the-autobahn/","tags":"Y2005"},"html":"<p>Today on the way to judo Christopher started singing the <a href=\"http://www.radiohamburg.de/\">Radio Hamburg</a> jingle... and we weren't even listening to Radio Hamburg. He then said he wanted to be a radio announcer, and proceeded to give a traffic report. On the Autobahn 7 traffic was being held up by hamsters on the roadway, and the police were asking motorists to avoid running over the hamsters.</p>\n<p>He then said it was just a joke, and that there weren't really any hamsters on the autobahn. Too bad that radio announcing seems to be a <a href=\"http://www.hebig.com/archives/003521.shtml\" title=\"Random radio by Heiko Hebig | hebig.com\">dying profession</a>. I don't even listen to Radio Hamburg, although Mama sometimes turns it on. The parody this morning of Angela Merkel singing Dancing Queen on her first day in office was kind of cute, though.</p>"},"next":{"fields":{"title":"Blogstats down, will be back","date":"23 Nov 2005","slug":"/2005-11-23-blogstats-down-will-be-back/"}},"previous":{"fields":{"title":"A Blog Named PapaTech","date":"24 Nov 2005","slug":"/2005-11-24-a-blog-named-papatech/"}}}],"pathPrefix":"","first":false,"last":false,"index":265,"pageCount":1101}}

